.app{
	background-image: url('https://i.pinimg.com/736x/95/da/2d/95da2d8e04bec4e1f274392bf322ac38.jpg');
    background-size: cover;
    background-position: bottom;
    transition: 0.4 ease-out; 
}
main{
	min-height: 100vh;
	background-image: linear-gradient(to bottom, rgba(0,0,0,0.2),rgba(0,0,0,0.75));
	padding: 25px;
}
.app.warm{
	background-image: url('https://images.assetsdelivery.com/compings_v2/windlaugh/windlaugh1812/windlaugh181200181.jpg');
}
.search-box{
	width: 100%;
	margin: 0 0 75px;
}
.search-box .search-bar{
	display: block;
	width: 100%;
	padding: 15px;
	appearance: none;
	background: none;
	border: none;
	outline: none;
	background-color: rgba(255, 255,255,0.5);
	border-radius: 0px 0px 16px 16px;
	margin-top: -25px;
	box-shadow: 0px 5px rgba(0,0,0,0.2);
	color: #313131;
	font-size: 20px;
	transition-timing-function: ease-in; 
	transition: 0.4s;
}
.search-box .search-bar:focus{
    background-color: rgba(255,255,255,0.75);	
}
.location-box .location{
	color: #FFF;
	font-size: 32px;
	font-weight: 500;
	text-align: center;
	text-shadow: 3px 3px rgba(50,50,70,0.5);
}
.location-box .date{
	color: #FFF;
	font-size: 20px;
	font-weight: 300;
	font-style: italic;
	text-align: center;
	text-shadow: 2px 2px rgba(50,50,70,0.5);
}
.weather-box{
	text-align: center;
}
.weather-box .temp{
	position: relative;
	display: inline-block;
	margin: 30px auto;
	background-color: rgba(255,255,255,0.2);
	border-radius: 16px;
	padding:  15px 25px;
	color: #FFF;
	font-size: 102px;
	font-weight: 900;
	text-shadow: 3px 6px rgba(50,50,70,0.5);	
	text-align: center;
	box-shadow: 3px 6px rgba(0,0,0,0.2);
}
.weather-box .weather{
	display: flex;
	justify-content: center;
	align-items: cent;
	color: #FFF;
	font-size: 48px;
	font-weight: 700;
	text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.sunTime{
	display: flex;
	justify-content: space-between;
	color: #FFF;
	font-size: 15px;
	font-weight: 500;
	text-align: center;
	text-shadow: 3px 3px rgba(50,50,70,0.5);
}
.sunrise{
	background-color: rgba(255,255,255,0.2);
	text-shadow: 3px 6px rgba(50,50,70,0.5);
	box-shadow: 3px 6px rgba(0,0,0,0.2);
	border-radius: 16px;
	padding:  5px 10px;
}
.sunset{
	background-color: rgba(255,255,255,0.2);
	text-shadow: 3px 6px rgba(50,50,70,0.5);
	box-shadow: 3px 6px rgba(0,0,0,0.2);
	border-radius: 16px;
	padding:  5px 10px;
}

